.areaMkt {
    z-index: 9999;
    display: none;
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
  }

.formMkt {
    position: absolute;
    height: 100vh;
    width: 80vw;
    display: block;
    justify-content: center;
    align-items: center;
    padding: 30px;
    margin-left: 8%;
    margin-top: 6%;
    background-color: #252525;
    border-radius: 30px;
    font-family: "Ubuntu";
}

.formMkt table {
  width: 50;
  border-spacing: 0;
  color: white;
  margin: auto;
}

.formMkt table th {
  background-color: rgb(255, 94, 0);
  border: 1px solid rgb(255, 94, 0);
}

.formMkt table td {
    background-color: #252525;
    height: 6.5vh;
    border: 1px solid #fb8218;
}
  
.formMkt table input {
    background-color: #252525;
    border: none;
    width: auto;
}

.opcao-select {
    width: 800px;
    height: 8vh;
    padding: 25px;
    font-size: 15px;
    border-radius: 3px;
    border: 1px solid white;
}

.opcao-select option {
    padding: 5px;
}
.import-select {
    width: 800px;
    padding: 25px;
    font-size: 15px;
    border-radius: 3px;
    border: 1px solid white;
}

.import-select option {
    padding: 5px;
}

.close_area4 {
    cursor: pointer;
    width: 20px;
    height: 20px;
    background-color: red;
    border-radius: 10px;
    position: absolute;
    border: none;
    left: 1.5%;
    top: 2%;
}
.dot2_area4 {
    cursor: pointer;
    width: 20px;
    height: 20px;
    background-color: rgb(248, 225, 14);
    border-radius: 10px;
    position: absolute;
    border: none;
    left: 3.5%;
    top: 2%;
}
.dot3_area4 {
    cursor: pointer;
    width: 20px;
    height: 20px;
    background-color: rgb(45, 202, 25);
    border-radius: 10px;
    position: absolute;
    border: none;
    left: 5.5%;
    top: 2%;
}

.titulo {
    text-align: center;
    margin-top: 3%;
    font-size: 3vi;
    color: white;
}

.duvidaMkt {
    background-image: url("../../images/request\ \(1\).png");
    width: 0.85vw;
    height: 1.8vh;
    float: right;
    cursor: pointer;
    position: absolute;
    left: 68%;
    top: 9%;
}

.setaEsquerda {
    background-image: url("../../images/seta-esquerda.png");
    background-repeat: no-repeat;
    background-size: contain;
    width: 10%;
    height: 10%;
    margin-bottom: -20%;
    margin-left: 10%;
    float: left;
    cursor: pointer;
}

.setaDireita {
    background-image: url("../../images/seta-direita.png");
    background-repeat: no-repeat;
    background-size: contain;
    width: 10%;
    height: 10%;
    margin-top: -2%;
    margin-right: -5%;
    float: right;
    cursor: pointer;
}
